import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listMembers } from '../../../../api/teachers';

import useAuth from '../../../../hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));


const ClassDetailView = (props) => {
  
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [members, setMembers] = useState([]);
  const { user } = useAuth();
  const sclassId = props.match.params.classId;
  const getMembers = useCallback(async () => {
    try {
      const members = await listMembers(sclassId);
      console.log(members);
      if (isMountedRef.current) {
        setMembers(members);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getMembers();}, [getMembers]);
console.log(user.userId);
  return (
    <Page className={classes.root} title="Giáo Viên">
      <Container maxWidth={false}>
        <Header sclassId={sclassId}/>
        <Box mt={3}>
          <Results members={members}/>
        </Box>
      </Container>
    </Page>
  );
};

export default ClassDetailView;
