import app from './_firebase';

import {getFirestore} from 'firebase/firestore'

import {random} from 'src/utils/stringutil';
export const addClass = async (data) => {
    try {
        const code = await random(6);
        var docRef = await app.firestore().collection("classes").doc(code);

        const doc = docRef.get()
        if (doc.exists) {
            return "existed";
        } else {
            await app.firestore().collection('classes').doc(code).set(data);
            console.log("add class success");
            // children:getFirestore().FieldValue.arrayUnion(String(student_id))

            await app.firestore().collection('schools').doc(data.school_id).update({classes:getFirestore().FieldValue.arrayUnion(docRef)})
        }
    } catch (err) {
        console.log(err);
    }
};

export const getClassApi = async (id) => {
    try {
        let querySnapshot  = await app.firestore().collection('classes').doc(id).get();
        const classes = querySnapshot.data()
        classes.id=id
        return classes
    } catch (err) {
        console.log(err);
    }
}

export const updateClass = async (data) => {
    try {
        console.log(data)
        let id=data.id;
        let result  = await app.firestore().collection('classes').doc(id).update(data);
        
        return result
    } catch (err) {
        console.log(err);
    }
};

export const listAllClasses = async (school_id) => {
    try {
        let querySnapshot  = await app.firestore().collection('classes').where("school_id","==",school_id).get();
        if (querySnapshot.docs) {
            const classes = querySnapshot.docs.map((doc) => {
              return {
                id: doc.id,
                ...doc.data()
              }
            })
            console.log(classes)
            return classes;
        }
    } catch (err) {
        console.log(err);
    }
}

export const listJsonClasses = async (school_id) => {
    try {
        let querySnapshot  = await app.firestore().collection('schools').doc(school_id).get();
        const dclasses = await querySnapshot.data().classes
        let a=[]
        for await (let val of dclasses){
            const doc =  await val.get()
            a[doc.id] = { id: doc.id, ...doc.data() }
        }
        return a;
    } catch (err) {
        console.log(err);
    }
}