import React, {
  useState,
  useCallback,
  useEffect
} from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Breadcrumbs,
  Button,
  Grid,
  Link,
  SvgIcon,
  Typography,
  makeStyles
} from '@material-ui/core';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import { PlusCircle as PlusCircleIcon } from 'react-feather';
import {getClassApi} from '../../../../api/classes.js';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
const useStyles = makeStyles((theme) => ({
  root: {},
  action: {
    marginBottom: theme.spacing(1),
    '& + &': {
      marginLeft: theme.spacing(1)
    }
  }
}));

const Header = ({ className,sclassId, ...rest }) => {
  const classes = useStyles();
  console.log("hear:",sclassId)
  const isMountedRef = useIsMountedRef();
  const [sclass, setClasses] = useState(null);

  const getClasses = useCallback(async () => {
    try {
      let sclass = await getClassApi(sclassId);
      console.log(sclass)
      if (isMountedRef.current) {
        setClasses(sclass);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getClasses();
  }, [getClasses]);

  return (
    <Grid className={clsx(classes.root, className)} container justify="space-between" spacing={3}{...rest}>
      <Grid item>
        <Breadcrumbs separator={<NavigateNextIcon fontSize="small"/>} aria-label="breadcrumb">
          <Link variant="body1" color="inherit" to="/teacher/management/classes" component={RouterLink}>
            Trang chủ
          </Link>
          <Link variant="body1" color="inherit" to="#" component={RouterLink}>
            Quản lý
          </Link>
          <Typography variant="body1" color="textPrimary">
              Lớp của tôi 
          </Typography>
        </Breadcrumbs>

        <Typography variant="h3" color="textPrimary">
          Danh sách lớp {sclass==null?"":sclass.name}
        </Typography>
      </Grid>
    </Grid>
  );
};

Header.propTypes = {
  className: PropTypes.string,
  sclassId:PropTypes.string
};

export default Header;
